// Implementeaz logica pentru pagina My Libraries, care permite utilizatorilor s gestioneze i s vizualizeze filmele salvate.
// Afieaz dou liste separate: Watched i Queue, utiliznd componenta libraryList.js.
// Coordoneaz funcionalitatea de comutare ntre tab-urile Watched i Queue, folosind componenta libraryTabs.js.
// Preia filmele salvate din localStorage sau dintr-o baz de date backend, utiliznd serviciul storage.js.
// Gestioneaz interaciunile utilizatorilor, precum eliminarea unui film dintr-o list sau deschiderea modalului pentru detaliile unui film.

import { getFromStorage } from '../app/localStorage';
import renderLibraryList from '../components/libraryList';
import initLibraryTabs from '../components/libraryTabs';


export default function initMyLibraryPage() {
  const libraryButtonsContainer = document.querySelector('#library-buttons');
  libraryButtonsContainer.style.display = 'block'; // Afieaz butoanele din header

  // Afieaz filmele din "WATCHED" implicit
  renderLibraryList(getFromStorage('watched'));

  // Initializeaz tab-urile folosind funcionalitatea butoanelor din header
  initLibraryTabs({
    onWatchedTabClick: () => {
      renderLibraryList(getFromStorage('watched'));
    },
    onQueueTabClick: () => {
      renderLibraryList(getFromStorage('queue'));
    },
  });
}
